/* WP Admin Bar*/

// Hide Unnecessary Items & Adding Icons
#wpadminbar .quicklinks .ab-empty-item {
	padding: 0 1.5em;
}
#wpadminbar ul#wp-admin-bar-root-default > li a {
    padding: 0 1.5em;
}
#wpadminbar ul#wp-admin-bar-root-default li+li {
	border-left: 1px dotted $gray-dark;
}
#wp-admin-bar-wp-logo,
#wp-admin-bar-comments,
#wp-admin-bar-my-account,
#wp-admin-bar-amf-close-admin-panel,
#wp-admin-bar-site-name .ab-sub-wrapper,
#wp-admin-bar-customize,
#wp-admin-bar-widgets {
    display: none !important;
}

// Make the non-official buttons gray
#wp-admin-bar-cxssh-main-menu,
#wp-admin-bar-epc_purge_menu,
#wp-admin-bar-query-monitor {
	background: $gray-dark !important;
	border-left-color: $black !important;
	float: right !important;
}

// "View Page" button icon
#wp-admin-bar-view > .ab-item::before,
#wp-admin-bar-archive > .ab-item::before,
#wp-admin-bar-stever-quotes-editall > .ab-item::before {
    content: "\f177";
    font-size: 1.3em;
	padding-top: .5em;
}
// "Endurance Cache" button icon
#wp-admin-bar-epc_purge_menu > .ab-item::before {
    content: "\f160";
    padding-top: 0.3em;
}
// "Query Monitor" button icon
#wp-admin-bar-query-monitor > .ab-item::before {
    content: "\f321";
    padding-top: 0.3em;
}

/* WP Admin Menu */
// Admin Menu Items: Hiding Unnecessary Ones
#menu-comments,
#menu-users,
#toplevel_page_gutenberg {
	display: none;
}